button {
  background-color: #007bff;
  color: white;
  border: none;
  padding: 10px 20px;
  margin: 10px;
  border-radius: 5px;
  cursor: pointer;
}

button:hover {
  background-color: #0056b3;
}

input {
  padding: 10px;
  margin: 10px;
  border-radius: 5px;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 50px;
}

.action-group {
  display: flex;
  align-items: center;
}

.container p {
  margin: 10px;
  align-items: left;
  text=align: left;
}

/* Left align paragraphs. */
.p {
  text-align: left;
} 
